import {request} from "../axios/request";

const baseUrl = '/api/admin'

const ArticleApi = {}

// 
ArticleApi.getArticles = () => {
  return request({
    url: `${baseUrl}/all`,
    method: 'get'
  })
}

ArticleApi.me = () => {
  return request({
    url: `${baseUrl}/me`,
    method: 'get'
  })
}

// 
ArticleApi.page = page => {
  return request({
    url: `${baseUrl}/page/${page}`,
    method: 'get'
  })
}

ArticleApi.loadArticlesByTag = params => {
  return request({
    url: `${baseUrl}/loadArticlesByTag`,
    params: params,
    method: 'get'
  })
}

// 
ArticleApi.query = params => {
  return request({
    url: `${baseUrl}/query`,
    params: params,
    method: 'get'
  })
}

// top
ArticleApi.listLatest = top => {
  return request({
    url: `${baseUrl}/latest`,
    params: {
      top: top
    },
    method: 'get'
  })
}

// Id
ArticleApi.get = id => {
  return request({
    url: `${baseUrl}/getArticleById`,
    method: 'get',
    params: {
      id
    }
  })
}

// 
ArticleApi.visit = articleId => {
  return request({
    url: `${baseUrl}/${articleId}`,
    method: 'put'
  })
}

// 
ArticleApi.count = () => {
  return request({
    url: `${baseUrl}/count`,
    method: 'get'
  })
}

// 
ArticleApi.create = article => {
  return request({
    url: `${baseUrl}/addArticle`,
    method: 'post',
    data: article
  })
}

// Id
ArticleApi.update = article => {
  return request({
    url: `${baseUrl}/updateArticle`,
    method: 'put',
    data: article,
  })
}

// 
ArticleApi.updateStatus = (id, status) => {
  return request({
    url: `${baseUrl}/updateStatus`,
    method: 'put',
    params: {
      id,
      status
    }
  })
}

// 
ArticleApi.updateDraft = (articleId, content) => {
  return request({
    url: `${baseUrl}/${articleId}/status/draft/content`,
    method: 'put',
    data: {
      content: content
    }
  })
}

// 
ArticleApi.delete = id => {
  return request({
    url: `${baseUrl}/delete`,
    method: 'delete',
    params: {
      id: id
    }
  })
}

// 
ArticleApi.articleStatus = {
  ALL: {
    value: 'All',
    color: 'red',
    status: 3,
    text: ''
  },
  PUBLISHED: {
    value: 'PUBLISHED',
    color: 'green',
    status: 1,
    text: ''
  },
  DRAFT: {
    value: 'DRAFT',
    color: 'yellow',
    status: 0,
    text: ''
  },
  RECYCLE: {
    value: 'RECYCLE',
    color: 'red',
    status: 2,
    text: ''
  }
}

export default ArticleApi